@mixin clearfix {
  &:after {
    content: '';
    clear: both;
    display: block; } }

@mixin size($w, $h: auto) {
	width: $w;
	height: $h; }

@mixin flexCenter {
	display: flex;
	justify-content: center;
	align-items: center; }

@mixin radius($radius: 100%) {
	border-radius: $radius; }

@mixin valign {
	position: absolute;
	top: 50%;
	transform: translateY(-50%); }
@mixin halign {
	position: absolute;
	left: 50%;
	transform: translateX(-50%); }
@mixin centeralign {
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%); }
